if (!window.IG$)
{
	window.IG$ = {};
}

IG$.__microloader = function(modules, callback) {
	var bc = ig$.bootconfig.boot,
		all_modules = [],
		i;
	
	while (mod = modules.shift())
	{
		var sc = bc[mod];
		
		if (sc && sc.length)
		{
			for (i=0; i < sc.length; i++)
			{
				all_modules.push(sc[i]);
			}
		}
		else if (mod.substr && mod.substr(mod.length - ".js".length) == ".js")
		{
			all_modules.push(mod);
		}
	}
	
	IG$.getScriptCache(
		all_modules, 
		new IG$.callBackObj(this, function() {
			callback && callback.apply(null);
			
			// fix support for existing customer with viewer.jsp
			if (!callback && IG$._source == "viewer")
			{
				$s.ready(function() {
					var viewer_inst = new IG$.amplix_instance({
						target: $("body")
					});
					
					viewer_inst.onLoad(function() {
						var me = this;
					});
					
					viewer_inst.create();
				});
			}
		}),
		mod == "appnc"
	);
};

IG$.getScriptCache = function(scripts, callback, nocache) {
	var loaded = [],
		head= document.head, // getElementsByTagName("head")[0],
		firstScript = document.scripts[0],
		__tscripts = [],
		sclength,
		nocache_key,
		smap = {};
	
	IG$.___loaded_scr = IG$.___loaded_scr || {}; 
	
	$.each(scripts, function(i, sc) {
		if (sc && !IG$.___loaded_scr[sc] && !smap[sc])
		{
			__tscripts.push(sc);
			smap[sc] = 1;
		}
	});
	
	sclength = __tscripts.length;
	
	var afterloaded = function() {
		if (loaded.length == sclength)
		{
			callback && callback.execute();
		}
	}
	
	if (nocache)
	{
		var dt = new Date();
		
		nocache_key = 
			"" + dt.getFullYear() + 
			(1+dt.getMonth()) +
			dt.getDate() +
			dt.getHours() + 
			"00" + // dt.getMinutes() +
			"00" // dt.getSeconds();
	}
		
	// watch scripts load in IE
	var loadScript = function(scs) {
			var sc = scs.shift(),
				scobj,
				script,
				is_css,
				ctag = "script",
				cache = nocache_key || ig$.bootconfig.cache;
			
			if (!sc)
			{
				return;
			}
			
			if (typeof(sc) == 'object' && sc.src)
			{
				scobj = sc;
				sc = sc.src;
			}
			
			is_css = sc.indexOf(".css") == sc.length - 4;
			ctag = is_css ? "link" : ctag;
			
			if (IG$.___loaded_scr[sc])
			{
				return;
			}
			
			IG$.___loaded_scr[sc] = 1;
			
			if ('async' in firstScript) // modern browsers
			{
				script= document.createElement(ctag);
				
				if (is_css)
				{
					script.rel = "stylesheet";
					script.type = "text/css";
					script.media = "all";
				}
				else
				{
					script.type= "text/javascript";
					script.async = false;
					
					if (scobj)
					{
						if (scobj.defer)
						{
							script.defer = scobj.defer;
						}
						
						if (scobj.async)
						{
							script.async = scobj.async;
						}
					}
				}
				
				script.onload = function() {
					loaded.push(sc);
					afterloaded();
				};

				script.onerror = function() {
					throw "dynamic loader : " + this.src;
				};
				
				if (is_css)
				{
					script.href = sc + "?sc=" + cache;
				}
				else
				{
					if (sc.indexOf("?") > 0)
					{
						script.src = sc;
					}
					else
					{
						script.src = sc + "?sc=" + cache;
					}
				}
				
				head.appendChild(script);
				
				// keep loading scripts
				loadScript(scs);
			}
			else if (firstScript.readyState) // IE < 10
			{
				// create a script and add it to our toto pile
				script = document.createElement(ctag);
				if (is_css)
				{
					script.rel = "stylesheet";
					script.type = "text/css";
					script.media = "all";
				}
				else
				{
					script.type= "text/javascript";
				}
				script.onreadystatechange = function() {
					// execute as many scripts in order as we can
					if (this.readyState == "loaded" || this.readyState == "complete")
					{
						// avoid future loading events from this script (ie if src changes)
						script.onreadystatechange = null;
						
						firstScript.parentNode.insertBefore(script);
						
						loaded.push(sc);
						// async loading
						// loadScript(scs);
						
						afterloaded();
					}
				};
				
				if (is_css)
				{
					script.href = sc + "?sc=" + cache;
				}
				else
				{
					script.src = sc + "?sc=" + cache;
				}
				loadScript(scs);
			}
			else
			{
				document.write("<script src='" + sc + "' type='text/javascript'></script>");
			}
		};
	
	if (!sclength)
	{
		sclength = 0;
		afterloaded();
	}
	else
	{
		loadScript(__tscripts);
	}
};

IG$.callBackObj = function(callerptr, callexec, callparam) {
	var me = this;
	me.p1 = callerptr;
	me.p2 = callexec;
	me.p3 = callparam;
}

IG$.callBackObj.prototype = {
	execute: function() {
		var me = this,
			arg = arguments,
			marg = [],
			i,
			ret;
		
		if (me.p2)
		{
			for (i=0; i < arg.length; i++)
			{
				marg.push(arg[i]);
			}
			if (me.p3 != null && typeof me.p3 != 'undefined')
			{
				marg.push(me.p3);
			}
			ret = me.p2.apply(me.p1 || null, marg);
		}
		
		return ret;
	}
};

IG$.ready = function(callback) {
	IG$._ready$ = IG$._ready$ || [];
	IG$._ready$.push(callback);
}

IG$.amplix_instance = function(opt) {
	var me = this;
	
	me.config = opt;
	
	me.target = opt.target;
	
	IG$.$a_id = IG$.$a_id || 0;
	
	me.id = "" + (IG$.$a_id++);
	
	IG$._insts$ = IG$._insts$ || {};
	
	IG$._insts$[me.id] = me;
	
	me.onLoad = function(callback) {
		me._ready$ = me._ready$ || [];
		me._ready$.push(callback);
	};
}

IG$.amplix_instance.prototype = {
	create: function() {
		var me = this;
		
		IG$.createInstance(me);
	},
	$load_init: function(tmpl, fm) {
		var me = this;
		if (me._ready$ && me._ready$.length)
		{
			$.each(me._ready$, function(i, f) {
				f.apply(me, tmpl, fm);
			});
			
			me._ready$ = null;
		} 
	},
	open_url: function(menu, callback) {
		if (menu.objid || menu.uid)
		{
			var me = this,
				mp = me.mainPanel,
				req = new IG$._rpc$();
			req.init(me, 
				{
					ack: "helpcontent",
					payload: {
						uid: menu.objid || menu.uid
					},
					mbody: {
						option: "translate"
					}
				}, me, function(jdoc) {
					var citem = jdoc && jdoc.length ? jdoc[0] : null,
						mp = me.mainPanel;
					if (citem)
					{
						mp.navigateApp.call(mp, citem.uid, citem.type.toLowerCase(), citem.name, citem.nodepath, true, citem.writable);
					}
					
					callback && callback.execute();
				});
			req.send(this);
		}
		else if (menu.jobid)
		{
			var me = this,
				mp = me.mainPanel,
				req = new IG$._rpc$(),
				jobid = menu.jobid,
				sid;

			if (jobid.indexOf(":") > 0)
			{
				sid = jobid.substring(jobid.indexOf(":") + 1);
				jobid = jobid.substring(0, jobid.indexOf(":"));
			}

			req.init(me, 
				{
					ack: "schedule",
					payload: {
						jid: jobid,
						action: "jobresult"
					},
					mbody: {}
				}, me, function(jdoc) {
					var citem = jdoc && jdoc.length ? jdoc[0] : null,
						mp = me.mainPanel,
						rpt;
					if (citem && citem.request && citem.request.data)
					{
						rpt = citem.request.data; 
						mp.navigateApp.call(mp, rpt.uid, rpt.type.toLowerCase(), rpt.name, rpt.nodepath, true, rpt.writable, {
							no_history: true,
							job_report_meta: {content: rpt},
							job_result: jdoc,
							__jid: jobid,
							__sid: sid || jobid
						});
					}
					
					callback && callback.execute();
				});
			req.send(this);
		}
	},
	
	getComponent: function(key) {
		var me = this,
			mp = me.mainPanel,
			cmp;
			
		if (mp)
		{
			cmp = mp.getComponent(key);
		}
		return cmp;
	},

	requestRoboAction: function(robo_inst, mbody, callback) {
		var me = this;
		if (me.mainPanel)
		{
			var r = me.mainPanel.requestRoboAction(robo_inst, mbody, callback);

			if (r == false)
			{
				callback.apply(robo_inst, [IRm$.r1("ROBO_ERR_TAB")]);
			}
		}
		else
		{
			callback.apply(robo_inst, [IRm$.r1("ROBO_ERR_NO_MAINPANEL")]);
		}
	},

	requestRoboFormat: function(robo_inst, mbody, callback) {
		var me = this;
		if (me.mainPanel)
		{
			var r = me.mainPanel.requestRoboFormat(robo_inst, mbody, callback);

			if (r == false)
			{
				callback.apply(robo_inst, [IRm$.r1("ROBO_ERR_TAB")]);
			}
		}
		else
		{
			callback.apply(robo_inst, [IRm$.r1("ROBO_ERR_NO_MAINPANEL")]);
		}
	},

	navigateCompleted: function(obj, type, opt) {
		var me = this,
			target = me.target,
			target_html = me.target_html,
			args = arguments;

		if (!target_html && target)
		{
			if(target && typeof(target) == "string")
			{
				me.target_html = target_html = $(target);
			}
			else if (target && typeof(target) == "object")
			{
				me.target_html = target_html = $(target);
			}
		}

		if (target_html)
		{
			void 0;
			target_html.trigger("instance_event", arguments);
		}
	}
}