var IRm$ = {};

IRm$.locale = [];

IRm$.r1 = function(rcsid, rvalue, dvalue) {
	if (!IRm$.currentLocale)
	{
		var i;
		
		IRm$.currentLocale = IRm$.locale[0];
		
		for (i=0; i < IRm$.locale.length; i++)
		{
			if (IRm$.locale[i].name == window.useLocale)
			{
				IRm$.currentLocale = IRm$.locale[i];
				break;
			}
		}
	}
	
	var r = rcsid; // ? (IRm$.currentLocale.data[rcsid] || '!RESOURCE_EMPTY!') : null;
	
	if (IRm$.currentLocale && IRm$.currentLocale.data[rcsid])
	{
		r = ig$.vmode == 1 ? rcsid + "*" : IRm$.currentLocale.data[rcsid];
	}
	else
	{
		r = (ig$.vmode == 1 ? "x " : "") + (dvalue ? dvalue : rcsid);
	}
	
	if (r && rvalue)
	{
		if (typeof(rvalue) == "string")
		{
			rvalue = [rvalue];
		}
		
		if (rvalue.length == 1)
		{
			r = r.replace('@', rvalue[0]);
		}
		else
		{
			for (i=0; i < rvalue.length; i++)
			{
				r = r.replace("@" + (i+1), rvalue[i]);
			}
		}
	}
	return r;
}

IRm$.r2_d/*loadResources*/ = function(bproc) {
	if (bproc && bproc.func)
	{
		bproc.func.call(bproc.scope);
	}
}

IRm$.r2/*loadResources*/ = function(bproc, b_sysconf) {
	var me = null,
		req = new IG$._rpc$(),
		lc = window.useLocale;
	
	IRm$.r3/*checkCuddler*/();
		
	req.init(me, 
		{
			ack: "content",
			payload: {uid: "/SYS_Config/locale/" + lc},
			mbody: {},
			mts: "ROOT"
		}, me, function(jdoc) {
			var t = jdoc,
				// tnodes,
				i, //j, snodes,
				// loc, sname, svalue,
				// sloc, 
				tloc;
				
			if (t && t.data)
			{
				tloc = {
					name: t.name,
					data: {}
				};
				
				for (i=0; i < t.data.length; i++)
				{
					tloc.data[t.data[i].name] = t.data[i].value;
				}
				
				IRm$.locale.push(tloc);
			}
			
			if (b_sysconf === false)
			{
				if (bproc && bproc.func)
				{
					bproc.func.call(bproc.scope);
				}
			}
			else
			{
				// getting ui configuration from system
				var req = new IG$._rpc$();
				req.init(me, 
					{
						ack: "content",
						payload: {uid: "/SYS_Config/ui_config"},
						mbody: {},
						mts: window.m$mts || "ROOT"
					}, me, function(jdoc) {
						if (jdoc.config)
						{
							$.each(jdoc.config, function(k, v) {
								if (k == "report_script" || k == "sheet_toolbar" || k == "application_title" || k == "application_title_html" || k == "custom_css")
								{
									ig$[k] = IG$._decodeVal(v);
								}
								else
								{
									if (k == "helper")
									{
										$.each(v, function(l, h) {
											h.value = IG$._decodeVal(h.value);
										});
									}
									ig$[k] = v;
								}
							});
						}

						if (ig$.custom_css)
						{
							IG$.impCSS(ig$.custom_css);
						}
						
						if (bproc && bproc.func)
						{
							bproc.func.call(bproc.scope);
						}
					}, function() {
						// if failed load default values
						if (bproc && bproc.func)
						{
							bproc.func.call(bproc.scope);
						}
					}
				);
				
				req.send(1);
			}
		}, function() {
			if (bproc && bproc.func)
			{
				bproc.func.call(bproc.scope);
			}
		});
	req.send(1);
}

IRm$.r3/*checkCuddler*/ = function() {
	var me = null,
		req = new IG$._rpc$();
	req.init(me, 
		{
			ack: "collaborate",
			payload: {cmd: "request_channel", istest: "T"},
			mbody: {},
			mts: "ROOT"
		}, me, function(jdoc) {
			if (!jdoc || (jdoc && (!jdoc.channel || !jdoc.instance)))
				return;

			let div_robo_wrap = $("#robo_wrap");

			if (div_robo_wrap.length)
			{
				let robo_wrap = $("#robo_wrap").show().draggable({
					containment: "parent" 
				});

				let tw = robo_wrap.parent().width(),
					th = robo_wrap.parent().height();

				robo_wrap.css({
					top: th - robo_wrap.height() - 20,
					left: tw - robo_wrap.width() - 20 - 170
				});

				let robo_main = $("#robo_main");

				robo_main
					.draggable({
						containment: "parent",
						handle: $(".robo_area > .robo_title", robo_main)
					})
					.resizable({
						containment: "parent",
						helper: "ui-resizable-helper",
						maxHeight: 800,
						maxWidth: 700,
						minHeight: 300,
						minWidth: 200
					});

				robo_main.css({
					top: th - robo_wrap.height() - 20 - robo_main.height() - 20,
					left: tw - robo_main.width() - 20
				});
			}
		}, function() {
			$("#robo_wrap").hide();
		});
	req.showerror = false;
	req.send(1);
}
